.form__container_type_auth {
  width: 396px
}

.form__error {
  align-self: flex-start;
  font: normal 400 10px/1.2 Inter, Arial, sans-serif;
  position: relative;
  text-align: left;
  width: 100%;
  height: 12px;
}

.form__admin_type_auth {
  margin-top: 0;
}

/* .form__footer_type_auth {
  flex-direction: row;
  font: normal 400 14px/1.21 Inter, Arial, sans-serif;
  justify-content: center
} */

.form__handlers_type_auth {
  margin: 0
}

.form__heading_type_auth {
  margin: 40px 0;
  text-align: start
}

.form__input-container_type_auth {
  margin: 0;
  padding: 0 0 132px
}

.form__item_el_email_type_auth {
  font-weight: 500
}

.form__item_type_auth {
  color: #FFF;
  font: normal 400 14px/1.21 Inter, Arial, sans-serif;
  text-align: start
}

.form__label_el_button {
  width: 100%
}

.form__label_type_auth {
  align-items: flex-start;
  flex-direction: column;
}

.form__label_type_auth :focus-visible+.line_form {
  border-color: #4285f4
}

.form__login-link {
  color: #4285F4;
  font: normal 400 14px/1.21 Inter, Arial, sans-serif
}

.form__question {
  color: #8B8B8B;
  margin-right: 6px;
}

.form__text_type_auth {
  color: #8B8B8B;
  font: normal 400 10px/1.2 Inter, Arial, sans-serif;
  margin-bottom: 5px;
  width: 100%
}

.line_form_type_auth {
  margin: 10px 0 6px
}

@media screen and (max-width: 769px) {
  .form__container_type_auth {
    width: 100%
  }

}


@media screen and (max-width: 481px) {

  .form__header_type_auth {
    width: 100%;
    display: flex;
    justify-content: center;
  }
}

@media screen and (max-height: 769px) {
  .form__input-container {
    padding-bottom: 70px;
  }

  .form__header_type_auth {
    margin: 30px 0;
    /* margin-top: 170pх; */
  }

}


@media screen and (max-height: 550px) {
  .form__input-container {
    padding-bottom: 30px;
  }

  .form__heading_type_auth {
    margin: 20px 0;
  }
}

@media screen and (max-width: 769px) {
  .form__container_type_auth {
    width: 100%
  }
}

@media screen and (max-width: 320px) {
  .form__label_type_auth {
    margin-bottom: 5px;
  }

  .form__input-container_type_auth_type_login {
    padding-bottom: 300px;
  }

  .form__header_type_auth {
    padding-top: 56px;
  }

  .form__question {
    font-size: 12px;
  }

  .form__login-link {
    font-size: 12px;
  }

  
  
}